<template>
  <div class="grid-container">
    <article v-for="post in posts" :key="post.id">
      <img :src="post.image" alt />
      <h3>{{post.description}}</h3>
       <h3 id = "postid">{{post.id}}</h3>
         <input v-model="update" type="text" id="update" placeholder="Nyt navn" prequired>
    <button v-on:click="updatePost" type="button">Update post</button>
     <button v-on:click="deletePost" type="button">Delete post</button>
    </article>
  </div>
</template>

<script>
import { postRef } from '../firebase-db'
export default {
  data () {
    return {
      update: '',
      posts: [],
      postRef
    }
  },
  firestore: {
    posts: postRef
  },
  methods: {

    updatePost () {
      const nameUpdate = {
        description: this.update
      }
      for (let i = 0; i < this.posts.length; i++) {
        postRef.doc(this.posts[i].id).update(nameUpdate)
        console.log(this.posts[i].id)
        const inputid = document.querySelector('#postid')
        console.log(inputid.textContent)
      }
    },
    deletePost () {
      postRef.doc(this.posts[0].id).delete()
    }
  }
}

